import React, { Component } from 'react';
import {Link} from 'react-router-dom';

class talentCard extends Component{
  render(){
    return (
        <div className="talentCardwrapper">
            <div className="talentCard">
                <div className="cardfooter">
                    <img src={this.props.cardImagePath} alt={this.props.imageAlt} />
                    <div class="namebox">
                      <h4>{this.props.cardName}</h4>
                      <h7>{this.props.cardTitle}</h7>
                      <p><strong>Email:</strong> <a href={this.props.cardEmailLink}> {this.props.cardEmail} </a></p>
                    </div>
                    <br class="clearer" />
                    {this.props.website}<br />
                    {this.props.linkedIn}
                    <p><strong>Located in</strong> {this.props.cardLocation}</p>
                    <p><strong>Open to</strong> {this.props.cardOpenTo}</p>
                    <p> {this.props.cardStatement}</p>
                </div>
            </div>
        </div>
        
    )
  }
}

export default talentCard;